.link {
    margin: 15px 0 15px 15px;
    width: 80px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: tomato;
    text-decoration: none;
    color: aliceblue;
    padding: 5px;
    border-radius: 8px;
    border: 2px solid #fff;
    transition-duration: 0.3s;

    &:hover {
        background: none;
        color: tomato;
        border: 2px solid tomato;
        transition-duration: 0.5s;
    }
}

.container {
    display: flex;
    justify-content: center;
    border-bottom: 2px solid #333;
    padding-bottom: 20px;
    column-gap: 20px;

    &_img {
        width: 310px;
        height: 450px;
    }

    &_info {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
        list-style: none;

        &_item {
            &_name {
                font-size: 28px;
            }

            &_title {
                font-size: 22px;

            }

            &_text {
                font-size: 16px;
            }

            &_genres {
                display: flex;
                column-gap: 10px;
            }
        }
    }
}

.container_additions {
    &_text {
        padding-left: 20px;
        font-weight: 600;
    }

    &_list {
        list-style: none;
        display: flex;
        flex-direction: column;
        row-gap: 10px;

        &_item {
            &_link {
                text-decoration: none;
                color: #333;
                font-weight: 400;
            }
        }
    }
}